
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
body {
  margin: 0;
  color: #e4d2d8;
  padding-top: 8%;
  font-family: sans-serif;
}
h2 {
  margin: 0px;
  font-size: xx-large;
}

.site-header {
    top: 0;
    left: 0;
    display: block;
    color: #e4d2d8;
    font-weight: bold;
    background-color: #d69090;
    opacity: 0.9;
    height: 8%;
    width: 100%;
    position: fixed;
}
.site-header-wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    margin: 0 5%;
}
.site-logo {
    width: 60%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: start;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    color: white;
    text-decoration: none;
}
h1 {
    font-size: medium;
    margin: 0;
    margin-left: 8px;
}
@media screen and (min-width: 480px) {
h1 {
        font-size: 24px;
}
}
.logo-img {
    height: 80%;
    border-radius: 4px;
}
.nav {
    width: 80%;
    text-align: right;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: end;
}
ul {
    list-style: none;
}
.header-li {
    height: 100%;
    display: inline-block;
    font-size: small;
    margin-left: 2px;
}
.menu-item {
    height: 100%;
    color: white;
    text-decoration: none;
}
@media screen and (min-width: 480px) {
.header-li {
        height: 100%;
        display: inline-block;
        font-size: large;
        margin-left: 8px;
}
}

.site-footer {
    display: block;
    color: white;
    font-weight: bold;
    width: 100%;
    background-color: gray;
}
.site-footer-wrapper {
    height: 100%;
    text-align: center;
}
.copy-right {
    margin: 0;
    padding: 20px;
}

.container {
  padding: 24px 10%;
}

.profile-wrapper {
    margin-top: 24px;
}
.profile-area {
    padding: 4% 20%;
    -webkit-box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
            box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
}
.profile-img {
    width: 100%;
    max-width: 250px;
    border-radius: 80%;
    display: inline-block;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin-top: 24px;
}
.name {
    font-weight: bold;
    font-size: x-large;
}
.sns-icons {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 50px;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.sns-link {
    margin-left: 8px;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.sns-img {
    width: 100%;
    max-width: 50px;
}

.skills-wrapper {
    margin-top: 24px;
    padding-top: 24px;
    padding-bottom: 28px;
    border-top: 2px solid gray;
    border-bottom: 2px solid gray;
}
.skills-boxes {
    display: block;
    height: 90%;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
.skills-box {
    display: block;
    height: 90%;
    padding: 8px 5%;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
            box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
}
@media screen and (min-width: 740px) {
.skills-boxes {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        height: 100%;
}
.skills-box {
        display: inline-block;
        width: 20%;
}
.skills-wrapper {
        height: 500px;
}
.skills-wrapper{
        border: none;
}
}
.skills-box h3{
    border-bottom: 1px solid gray;
}
table {
    width: 100%;
    font-size: small;
    border-collapse: collapse;
}
td {
    border-bottom: solid 1px #cccccc;
}
th {
    border-bottom: solid 1px #cccccc;
}

.works-wrapper {
    margin-top: 24px;
}
.works-area {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    text-align: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.works-card {
    width: 40%;
    -webkit-box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
            box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
}

.certifications-wrapper {
    margin-top: 24px;
}
.certifications-area {
    padding: 4% 20%;
    text-align: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
            box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
}
.certifications-table {
}
.certifications-li {
    border-bottom: solid 1px #cccccc;
}
